.settings {
    margin-top: 10vh;
    display: flex;
}

.settings div {
    font-family: Arial, sans-serif;
    margin: 0 auto;
}

.settings h1 {
    font-size: 24px;
    color: var(--color-white);
    margin-bottom: 20px;
}

.settings h2 {
    font-size: 20px;
    color: var(--color-light-blue);
    margin-top: 30px;
    margin-bottom: 15px;
    border-bottom: 1px solid var(--color-lighter-gray);
    padding-bottom: 5px;
}

.settings p {
    font-size: 16px;
    color: var(--color-white);
    margin-bottom: 10px;
}

.settings .passwordChangeInputs{
    width: 60%;
    margin: 0;
}

.settings input[type="password"] {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
    border: 1px solid var(--color-lighter-gray);
    border-radius: 5px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.3s;
}

.settings input[type="password"]:focus {
    border-color: var(--color-primary-blue);
}

.settings .errorMessage {
    color: var(--color-red);
}

.settings .successMessage {
    color: var(--color-green);
}

.settings button {
    background-color: var(--color-primary-blue);
    color: var(--color-white);
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.settings button:hover {
    background-color: var(--color-dark-blue);
}
